}

.sort-vocalist-list {
    background-color: white;
    border: none;
    border-radius: 4px;
    color: #404040;
    padding: 1vh;
    width: 90%;
    -webkit-order: 5;
            order: 5;
}

.sort-vocalist-links {
    -webkit-flex-basis: 100%;
            flex-basis: 100%;
    -webkit-flex-grow: 1;
            flex-grow: 1;
    -webkit-flex-shrink: 1;
            flex-shrink: 1;
    font-size: calc(7px + 0.8vmin);
    -webkit-order: 6;
            order: 6;
    padding-top: 1%;
    padding-bottom: 1%;
    text-align: center;
}

.sort-vocalist-button {
    background-color: #F4511E;
    border-radius: 4px;
    border: none;
    color: #FFFFFF;
    cursor: pointer;
    font-size: calc(6px + 0.8vmin);
    padding: 1vh 3vh 1vh 3vh;
    margin-right: 3vh;
    text-align: center;
}

.Dropdown-menu-type {
    background-color: white;
    border-radius: 4px;
    color: #404040;
    cursor: pointer;
    min-width: 100px;
    max-height: 200px;
    overflow-y: auto;
    position: absolute;
    text-align: left;
    width: 60%;
}

.Dropdown-control {
    color: #404040;
    cursor: pointer;
    font-family: Arial, Helvetica, sans-serif;
    font-size: calc(9px + 0.8vmin);
}

.Dropdown-option {
    box-sizing: border-box;
    color: rgba(51, 51, 51, 0.8);
    cursor: pointer;
    display: block;
    font-size: calc(9px + 0.8vmin);
