  }

.Dropdown-option:hover {
    background-color: #eeeeee;
    color: #333;
  }

.Dropdown-icon, .Dropdown-icon-mobile {
    position: relative;
    float: right;
    padding-right: 10px;
}

.Dropdown-icon {
    bottom: calc(1px + 1vmin);
}

.Dropdown-icon-mobile {
    bottom: calc(5px + 1vmin);
}
.slideshow-section {
    height: auto;
    max-width: 100%;
    position: relative;
    overflow: hidden;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
}

.slideshow {
    display: inline;
    vertical-align: top;
    float: none;
    -webkit-animation: moveSlideshow 24s linear infinite;
            animation: moveSlideshow 24s linear infinite;
}

@-webkit-keyframes moveSlideshow {
    100% { 
      -webkit-transform: translateX(-55.4%); 
              transform: translateX(-55.4%);  
    }
}

@keyframes moveSlideshow {
    100% { 
      -webkit-transform: translateX(-55.4%); 
              transform: translateX(-55.4%);  
    }
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.app-header {
  background-color: #282c34;
  min-height: 13vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(12px + 2vmin);
  line-height: 1vh;
  color: white;
}

.app-subtitle {
  line-height: 0vh;
  font-size: calc(3px + 2vmin);;
  padding-bottom: 4vh;
}

.app-body {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  line-height: 1vh;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
