.sort-vocalist-section {
    align-content: center;
    align-items: baseline;
    background-color: rgb(236, 236, 236);
    display: flex;
    margin-top: 2vh;
    margin-bottom: 15vh;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-start;
    padding-bottom: 1vh;
    width: 80%;
}

.sort-vocalist-addvox, .sort-vocalist-sort {
    flex-basis: 17%;
    flex-grow: 2;
    flex-shrink: 2;
    font-size: calc(8px + 1.1vmin);
    font-weight: bold;
    line-height: 3vh;
    padding: 10px;
    text-align: center;
}

.sort-vocalist-addvox {
    order: 1;
}

.sort-vocalist-sort {
    order: 4;
}

.sort-vocalist-addvox-dropdown, .sort-vocalist-sort-dropdown {
    flex-basis: 80%;
    flex-grow: 4;
    flex-shrink: 4;
    font-size: calc(2px + 2vmin);
    padding-left: 15px;
}

.sort-vocalist-addvox-dropdown {
    order: 2;
}

.sort-vocalist-sort-dropdown {
    order: 5;
}

.sort-vocalist-addvox-button {
    order: 3;
}

.sort-vocalist-list {
    background-color: white;
    border: none;
    border-radius: 4px;
    color: #404040;
    padding: 1vh;
    width: 90%;
    order: 5;
}

.sort-vocalist-links {
    flex-basis: 100%;
    flex-grow: 1;
    flex-shrink: 1;
    font-size: calc(7px + 0.8vmin);
    order: 6;
    padding-top: 1%;
    padding-bottom: 1%;
    text-align: center;
}

.sort-vocalist-button {
    background-color: #F4511E;
    border-radius: 4px;
    border: none;
    color: #FFFFFF;
    cursor: pointer;
    font-size: calc(6px + 0.8vmin);
    padding: 1vh 3vh 1vh 3vh;
    margin-right: 3vh;
    text-align: center;
}

.Dropdown-menu-type {
    background-color: white;
    border-radius: 4px;
    color: #404040;
    cursor: pointer;
    min-width: 100px;
    max-height: 200px;
    overflow-y: auto;
    position: absolute;
    text-align: left;
    width: 60%;
}

.Dropdown-control {
    color: #404040;
    cursor: pointer;
    font-family: Arial, Helvetica, sans-serif;
    font-size: calc(9px + 0.8vmin);
}

.Dropdown-option {
    box-sizing: border-box;
    color: rgba(51, 51, 51, 0.8);
    cursor: pointer;
    display: block;
    font-size: calc(9px + 0.8vmin);
    padding: 10px 10px;
  }

.Dropdown-option:hover {
    background-color: #eeeeee;
    color: #333;
  }

.Dropdown-icon, .Dropdown-icon-mobile {
    position: relative;
    float: right;
    padding-right: 10px;
}

.Dropdown-icon {
    bottom: calc(1px + 1vmin);
}

.Dropdown-icon-mobile {
    bottom: calc(5px + 1vmin);
}